body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
    background: lightblue;
    padding-left: 4px;
}

.header {
    text-align: center;
    background: lightgreen;
    font-size: 2em;
}

.menu {
    border-top: 1px solid black;
    background: lightblue;
    text-align: center;
}

.menu button {
    margin-right: 10px;
    background: lightgray;
}

.menu:last-child {
    margin-right: 0;
}

.prooflist {
    background: white;
    width: 100%;
}

.prooflist tbody tr th {
    border: 1px solid black;
}

.prooflist tbody tr td {
    border: 1px solid black;
}

.prooflist {
    border-collapse: collapse;
}

.nosequents {
    text-align: center;
}

.proofsequent {
    border: 1px solid black;
}

.proofsequent td {
    border: 1px solid black;
}

.proofediting {
    background: yellow;
}

.seqselected {
    background: orange;
}

.seqref {
    text-align: right;
}

.seqsel {
    text-align: center;
}

.seqvalid {
    background: green;
}

.seqinvalid {
    background: red;
}

.donebutton {
    background: lightblue;
}

.renamebutton {
    background: orange;
}

.deletebutton {
    background: red;
}

.editbutton {
    background: yellow;
}

.checkbutton {
    background: lightgreen;
}

.seqmove {
    text-align: center;
}

.seqid {
    text-align: center;
}

.seqactions {
    text-align: center;
}

.seqrule {
    text-align: center;
}

.seqrule input {
    color: black;
}

body {
    background: white;
}

